@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Lato Regular'), local('Lato-Regular'), url(https://fonts.gstatic.com/s/lato/v16/S6uyw4BMUTPHjx4wWw.ttf) format('truetype');
}
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local('Lato Bold'), local('Lato-Bold'), url(https://fonts.gstatic.com/s/lato/v16/S6u9w4BMUTPHh6UVSwiPHA.ttf) format('truetype');
}
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: local('Lato Black'), local('Lato-Black'), url(https://fonts.gstatic.com/s/lato/v16/S6u9w4BMUTPHh50XSwiPHA.ttf) format('truetype');
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  text-align: left;
  background: black;
  color: white;
  -webkit-font-smoothing: antialiased;
}
body h3 {
  font-family: "Inconsolata", monospace;
  color: #8797b2;
  text-transform: uppercase;
  font-size: 15px;
  font-weight: 400;
  margin: 0;
}
body .main__UI-view,
body .main__manifest-view {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
}
body .main__error {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
  margin-top: -20px;
  padding-bottom: 20px;
  font-size: 13px;
  font-weight: 600;
  color: #ff205d;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
body .main__error.-show {
  visibility: visible;
  opacity: 1;
}
body .main__column.-last .data-block {
  margin-right: 0;
}
body .main__row {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: row;
  align-items: stretch;
}
body .main__row .data-block {
  flex: 1 1 50%;
  margin-right: 15px;
  margin-bottom: 15px;
}
body .main__row .data-block:last-of-type {
  margin-right: 0;
}
body .main__row .data-block--video {
  flex-basis: 40%;
}
.switch-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin-right: 0;
}
.switch-wrap span {
  color: white;
  font-size: 13px;
  margin: 0 10px;
  font-weight: 600;
}
.switch {
  position: relative;
  display: inline-block;
  min-width: 60px;
  height: 34px;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #475470;
  border: 1px solid white;
  border-radius: 34px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 3px;
  bottom: 3px;
  border-radius: 50%;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}
.header {
  background: #8797b2;
  height: 60px;
}
.header__content {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
  padding-left: 20px;
  padding-right: 20px;
}
.header h1 {
  color: white;
  font-size: 24px;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
}
.header h1 {
  margin: 0 20px;
}
.header .logo {
  margin-right: 20px;
}
.data-block {
  background: #262a35;
  border-radius: 4px;
  padding: 20px;
}
.data-block h2,
.data-block p {
  color: white;
  font-weight: 400;
}
.data-block h2 {
  font-size: 24px;
}
.data-block h3 {
  margin-bottom: 20px;
}
.data-block h4 {
  font-size: 19px;
  margin: 0 0 10px 0;
  font-weight: 400;
}
.data-block small {
  font-size: 13px;
  display: block;
  margin-top: -5px;
}
.data-block p {
  font-size: 16px;
  margin-bottom: 0;
}
.data-block p:first-of-type {
  margin-bottom: 40px;
}
.data-block img {
  width: 100%;
  border-radius: 3px;
}
.data-block__iab-row {
  display: flex;
}
.data-block__iab-block {
  padding: 0 40px 5px 40px;
  border-right: 1px solid rgba(217, 221, 230, 0.2);
}
.data-block__iab-block:first-of-type {
  padding-left: 0;
}
.data-block__iab-block:last-of-type {
  border-right: none;
}
.data-block .pui-tag-list {
  margin-bottom: 0;
}
.data-block .pui-thumbnail {
  min-width: 404px;
  min-height: 228px;
  width: 100%;
}
.source-table {
  margin-right: 15px;
}
.source-table__headers {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  padding: 15px;
}
.source-table__body {
  margin-right: 0;
}
.source-table h3,
.source-table span {
  min-width: 90px;
  font-size: 15px;
  margin-right: 10px;
}
.source-table__row {
  background: #262a35;
  border-radius: 4px;
  padding: 10px 20px;
  height: 54px;
  margin-bottom: 5px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
}
.source-table__row span {
  display: inline-block;
  color: white;
  font-weight: 300;
  font-family: "Inconsolata", monospace;
}
.source-table__row span:first-of-type {
  font-weight: 600;
}
.source-table__row span:last-of-type {
  color: #98DBEF;
}
.media-ids {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
  padding-top: 30px;
  padding-bottom: 30px;
}
.media-ids h3 {
  margin-right: 20px;
  font-weight: 600;
}
.media-ids .pui-text-input {
  max-width: 150px;
  margin-right: 8px;
}
.media-ids .pui-button {
  margin-right: 40px;
}
.media-ids__id {
  font-size: 12px;
  padding: 10px 0;
  color: white;
  margin: 0 15px;
  cursor: pointer;
  border: 2px solid transparent;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.media-ids__id.-is-active {
  padding-bottom: 10px;
  border-bottom: 2px solid #ec0041;
}
.manifest {
  background: #21252e;
  border-radius: 4px;
  padding: 40px;
}
.manifest pre {
  font-size: 15px;
  background: #21252e !important;
}
.manifest .__json-key__ {
  color: #e86a5c !important;
}
.manifest .__json-string__ {
  color: #f7c966 !important;
}
.manifest .__json-value__ {
  color: #74c1e0 !important;
}

